:root {
  --mint: #3cb489;
  --mint-light: #7dd8b5;
  overflow: hidden;
}

.App {
  text-align: center;
  display: flex;
  min-height: 100vh;
  min-height: -webkit-fill-available;
  align-items:center;
  justify-content: center;
  font-family: 'Montserrat', sans-serif;
  overflow: hidden;
  padding-top: 10%;
}

.Content {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: row;
  width: 100%;
  height: 100%;
}

.Body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
/*  padding: 5%;*/
}

.Title {
  font-weight: bold;
  font-size: xx-large;
  color: var(--mint);
}

.Item {
  display: flex;
  flex-direction: column;
  padding: 12px;
}

.Item-Title {
  font-weight: bold;
}

.Item-Details {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.SubItem {
  padding-top: 6px;
  padding-left: 6px;
  padding-right: 6px;
}

a {
  color: var(--mint);
}

a:hover {
  color: var(--mint-light);
}

.Logo {
  height: 200px;
  width: 200px;
}

.Logo img {
  height: 100%;
  width: 100%;
}

.Date {
  color: gray;
}

.GlassesOverlay {
 z-index: 1;
 position: fixed;
 padding-top: 65px;
 padding-left: 31px;
 justify-content: center;
/* border: 2px solid blue;*/
}

.Glasses {
  display: flex;
  justify-content: space-around;
  transition: transform ease 300ms;
}

.Glasses:hover {
  transform: translateY(-3px);
}

.InfiniteAnimation {
    animation: MoveUpDown 1s linear infinite;
}

.LeftCircle {
  border: 2px solid black;
  border-radius: 50%;
  width: 38px;
  height: 38px;
  margin-right: 5px;
  margin-left: 7px;
}

.RightCircle {
  border: 2px solid black;
  border-radius: 50%;
  width: 38px;
  height: 38px;
}

@keyframes MoveUpDown {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(4px);
  }
}